













import Vue from 'vue';
import { FCheckboxGroupField, FCheckboxGroup } from '@fkui/vue';
import { TodoItem, TodoService } from '@/services/TodoService';

export default Vue.extend({
    name: 'TodoList',
    components: { FCheckboxGroup, FCheckboxGroupField },
    data: () => ({
        todos: new Array<TodoItem>(),
    }),
    created(): void {
        this.todos = TodoService.getTodos();
    },
});
