export class TodoItem {
    public id: number;
    public text: string;
    public isDone: boolean;
    public longText: string;

    public constructor(id: number, text: string, longText: string, isDone = false) {
        this.id = id;
        this.text = text;
        this.longText = longText;
        this.isDone = isDone;
    }
}

export interface TodoServiceInterface {
    getTodos(): TodoItem[];
    addTodo(text: string, longText: string): void;
}
