import { TodoServiceInterface, TodoItem } from './TodoServiceInterface';

// Do not export the implemention class.
// A service shall implement an interface with
// the exposed methods.
class TodoServiceImpl implements TodoServiceInterface {
    private todos: TodoItem[] = [
        new TodoItem(1, 'Tvätta', 'Glöm inte rensa filtret i torktumlaren'),
        new TodoItem(2, 'Titta TV', 'Ikväll är det Vilda Västern-dokumentär'),
    ];

    public getTodos(): TodoItem[] {
        return this.todos;
    }

    public addTodo(text: string, longText: string): void {
        const id = this.todos.length + 1;
        const todo = new TodoItem(id, text, longText);
        this.todos.push(todo);
    }
}

// Export a single instance of the service (singleton pattern).
export const TodoService = new TodoServiceImpl();
