import '@fkui/logic/lib/polyfills';

import App from '@/App.vue';
import LoadingApp from '@/LoadingApp.vue';
import router from '@/router';
import '@fkui/design/lib/default.min.css';
import Vue, { VNode } from 'vue';
import { TranslationService } from '@fkui/logic';
import { TranslationPlugin, ValidationPlugin, TestPlugin } from '@fkui/vue';

Vue.config.productionTip = false;

Vue.use(ValidationPlugin);
Vue.use(TestPlugin);

async function createLoadingApp(): Promise<void> {
    new Vue({
        render: (h): VNode => h(LoadingApp),
    }).$mount('#app');
}

async function loadTranslations(): Promise<void> {
    const translateFunction = await TranslationService.createTranslateFunction({ language: 'sv', namespace: 'translations' });
    try {
        await TranslationService.loadTranslations('/fkui/example/api/translations', { language: 'sv', namespace: 'translations' });
    } catch (error) {
        // Ignore error silently.
    } finally {
        Vue.use(TranslationPlugin, { translateFunction });
    }
}

async function createApp(): Promise<void> {
    new Vue({
        router,
        render: (h): VNode => h(App),
    }).$mount('#app');
}

createLoadingApp()
    .then(loadTranslations)
    .then(createApp);
