









import { FTextField, FTextareaField } from '@fkui/vue';
import Vue from 'vue';
import { TodoService } from '@/services/TodoService';

export default Vue.extend({
    name: 'TodoAdd',
    components: { FTextField, FTextareaField },
    data: () => ({
        todoText: '',
        todoLongText: '',
    }),
    methods: {
        onClickAddTodo(): void {
            if (this.todoText !== '') {
                TodoService.addTodo(this.todoText, this.todoLongText);
                this.todoText = '';
                this.todoLongText = '';
            } else {
                alert('Sysslan behöver en text!');
            }
        },
    },
});
